<template>
    <div class="number flex-row-start">
        <img src="~@/assets/images/warehouse/reduce.png" class="number-btn pointer" @click="reduce"/>
        <input type="text" class="text-center number-input flex-1" v-model="num" />
        <img src="~@/assets/images/warehouse/add.png" class="number-btn pointer" @click="add"/>
    </div>
</template>

<script>
export default {
    name: 'NumberInput',
    props: {
        value: {
            type: Number,
            default: 100
        }
    },
    data() {
        return {
            num: 100
        }
    },
    mounted() {
        this.num = this.value
    },
    methods: {
        reduce() {
            this.num -= 100
            if (this.num <= 1) {
                this.num = 1
            }
        },
        add() {
            this.num += 100
        }
    }
}
</script>

<style lang="scss" scoped>
    .number{
        width: 1.14rem;
        height: .36rem;
        background: #473E39;
        border: 1px solid #494949;
        border-radius: .18rem;
        .number-btn{
            width: .34rem;
            height: .34rem;
        }
        .number-input{
            background: none;
            color: #FC802F;
            font-size: .18rem;
            line-height: .34rem;
        }
    }
</style>